export class Widget {
  _id: String;
  type: String;
  pageId: String;
  size: String;
  width: String;
  text: String;
  url: String;
  rows: Number;
  name: String;
  formatted: Boolean;
  placeholder: String;
  constructor(_id: String, type: String, pageId: String) {
    this._id = _id;
    this.type = type;
    this.pageId = pageId;
  }
}



